<template>
    <div class="zhuce">
   <div class="zhu">
       <h2>登录</h2>
        <el-input class="in1" 
         placeholder="请输入用户名"
         v-model="userName" type="text" name="userName">
        <i slot="prefix" class="el-input__icon el-icon-user-solid"></i>
    </el-input>
    <div class="err1">{{err1}}</div>
  <el-input class="in2" @keyup.native.enter="deng"
    placeholder="请输入密码"
     v-model="passWord" type="password" name="passWord">
    <i slot="prefix" class="el-input__icon el-icon-s-goods"></i>
  </el-input>
   <div class="err2">{{err2}}</div>
  <div class="btn" @click="deng">登录</div>
  <el-checkbox v-model="checked" class="jizhu">记住密码</el-checkbox>
  <router-link to="/register"><div class="zh">注册账号>>></div></router-link>
   </div>
  
  

        
        
    </div>
</template>
<script>
export default {
    data(){
        return{
            userName:"",
            passWord:"",
             err1:"",
            err2:"",
            checked: false
        }
    },
    created(){
        this.userName=JSON.parse(localStorage.getItem("gk"))
       this.passWord=JSON.parse(localStorage.getItem("gb"))
       if(!this.userName.length==""){
           this.checked=true
       }

    },
   
       methods: {
        deng:async function(){
           
            if(this.userName==""){
                this.err1="用户名不能为空";
                return false
            }
            var patt1 = /^[a-zA-Z0-9_\u4e00-\u9fa5]{6,12}$/
            if(!patt1.test(this.userName)){
                this.err1="用户名只能为六到十二位的汉字、数字、字母、下划线";
                return false
            }
             this.err1=""
             
            if(this.passWord==""){
                this.err2="密码不能为空";
                return false
            }
            var patt2 = /^[a-zA-Z0-9_]{6,12}$/
            if(!patt2.test(this.passWord)){
                this.err2="密码只能为六到十二位数字、字母、下划线";
                return false
            }
             this.err2=""
           


            
            var res = await this.$ajax('/login?userName='+this.userName+'&password='+this.passWord)
          
            if(res.data.code){
                this.$message({
                            type:"success",
                            message:"恭喜您登录成功"
                        })
                        
                
                     this.$store.commit("setToken",{
                                token:res.data.token,
                                userName:res.data.userName
                            })
                     localStorage.setItem("userInfo",JSON.stringify({
                                token:res.data.token,
                                userName:res.data.userName
                            }))
                            if(this.checked){
                                localStorage.setItem("gk",JSON.stringify(this.userName))
                                 localStorage.setItem("gb",JSON.stringify(this.passWord))
                                
                            }else{
                                  localStorage.setItem("gk","")
                                 localStorage.setItem("gb","")

                                

                            }
                
               
             
              
            setTimeout(()=>{
                this.$router.replace(this.$route.query.url||"/");
                this.userName=this.passWord='';
                  this.err1=this.err2=""
                },2000)

                
               
                
            }else{
                this.$message.error('您的昵称已被占用');
                this.userName=this.passWord='';
                  this.err1=this.err2=""
                return false
            }
             setTimeout(()=>{
                this.userName=JSON.parse(localStorage.getItem("gk"))
                this.passWord=JSON.parse(localStorage.getItem("gb"))

                },2000)
           
        }
       
        }
    
}
</script>
<style scoped>
.zhuce{
    width: 450px;
    height: 320px;
    background: #fafafa;
    margin: 0 auto;
    padding: 20px;
   
}
.zhu{
    width: 300px;
    height: 310px;
    margin: 0 auto;
 
    position: relative;
  
}
.jizhu{
    position: absolute;
     left: 5px;
   bottom: 10px;
}
.zh{
   position: absolute;
   right: 10px;
   bottom: 10px;
     color: rgb(160, 160, 243)
}

.zhu h2{
    text-align: center;
   
    color: rgb(160, 160, 243);
    
}
.btn{
    width: 100%;
    height: 40px;
    line-height: 40px;
    text-align: center;
    margin-top: 50px;
    border-radius: 6px;
    background:rgb(160, 160, 243);
    color: white;
   
}
.in1{
    margin-top: 20px
}
.in2{
    margin-top: 50px
}
.err1{
    color: orangered;
    position: absolute;
    top: 96px;
    left: 20px;
    font-size: 14px
   
}
.err2{
    color: orangered;
    position: absolute;
    top: 190px;
    left: 20px;
    font-size: 14px
   
}
    
</style>